.top-container {
    background-image: url("../../assests/images/pattern-bg.png");
    background-repeat: inherit;
    color: white;
    text-align: center;
    padding-bottom: 100px;
    padding-top: 30px;
    z-index: 0;
}

.top-container > h1 {
    padding: 25px;
}
.top-container > form {
    display: flex;
    width: 100%;
    justify-content: center;
}

.top-container > form > input {
    padding: 15px;
    width: 450px;
    outline: none;
    border: none;
    border-radius: 15px 0px 0px 15px;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.top-container > form > button {
    height: 56px;
    width: 56px;
    border-radius: 0 15px 15px 0;
    outline: none;
    border: none;
    background-color: black;
    cursor: pointer;
}

@media only screen and (max-width: 576px) {
    .top-container > h1 {
        font-size: 24px;
    }
    .top-container > form > input {
        width: 300px;
        font-size: 15px;
    }    
}
