.Data-container {
    position: absolute;
    top: 200px;
    left: 10%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    z-index: 1000;
    background-color: white;
    width: 80%;
    margin: auto;
    border-radius: 20px;
    align-items: center;
}
.item {
    padding: 30px;
}
.adjust-1 {
    color: #eee;
    transform: scaleY(3);
}
@media only screen and (max-width: 1081px) {
    .Data-container {
        flex-direction: column;
        text-align: center;
    }
    .adjust-1 {
        display: none;
    }
  }
